.input_container {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 1.5rem;
  margin-bottom: 1rem;
  height: auto;
}

.input {
  background: transparent;
  border: 2px solid #fbe0f3;
  outline: none;
  height: 100%;
  max-width: 44.5rem;
  width: 100%;
  font-size: 1.5rem;
  color: #fbe0f3;
  padding: 1.5rem 1.5rem 1.5rem 1rem;
}

.input::placeholder {
  color: rgba(251, 224, 243, 0.5);
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  text-align: start;
  border: 0;
  letter-spacing: 4px;
}

.input_wrapper {
  position: relative;
  width: 100%;
}

.utils {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  letter-spacing: 4px;
  line-height: 1.5rem;
  font-weight: 400;
  cursor: pointer;
}

.utils P:nth-of-type(1) {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.login-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.btn_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.error {
  color: #eb443c;
  max-height: 0;
  height: 100%;
  overflow: hidden;
  transition:
    max-height 0.3s ease-in-out,
    opacity 0.3s ease-in-out;
  font-size: 1rem;
  padding: 0;
  margin: 0;
  font-weight: 400;
  letter-spacing: 4px;
  opacity: 0;
  text-align: center;
  line-height: 1.5rem;
  white-space: pre-line;
}

.input_container .error {
  margin-top: -0.5rem;
}

.error_border {
  border-color: rgba(201, 42, 21, 0.85);
  outline: none;
}

.btn {
  padding: 1rem 11rem;
  font-size: 1.5rem;
  font-weight: 200;
  line-height: 0;
  background: transparent;
  border: 2px solid #fbe0f3;
  color: #fbe0f3;
  cursor: pointer;
  letter-spacing: 0.25rem;
  display: flex;
  align-items: center;
  border-radius: 2rem;
  margin-top: 3.5rem;
  margin-bottom: 1rem;
  gap: 1.5rem;
}

.visible {
  opacity: 1;
  max-height: 1.5rem;
}

.utils .error.visible {
  max-height: 3rem;
}

.wrapper_header {
  left: 0;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1920px;
  width: 100%;
  padding: 0 4rem 0 3rem;
  position: relative;
}

.wrapper_header button {
  color: black;
  background: none;
  border: 0;
  padding: 0;
  display: inline-block;
  cursor: pointer;
  height: 2rem;
  color: white;
  transition: all 0.15s ease-in-out;
}

.wrapper_header h2 {
  font-size: 2rem;
  font-weight: 400;
  color: #feffb3;
  padding: 1rem 0 1.25rem 0rem;
  line-height: 2.375rem;
  cursor: pointer;
}

.container {
  max-height: 50vh;
  height: 100%;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
  max-width: 44.5rem;
  width: 100%;
  z-index: 10;
  border-radius: 3px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.buttons {
  display: flex;
  width: 100%;
  gap: 4rem;
  margin-bottom: 3rem;
}

.selected {
  text-decoration: underline;
  text-underline-offset: 7px;
  text-decoration-thickness: 2px;
  color: #feffb3 !important;
}

.nav-button {
  background: transparent;
  border: none;
  padding: 0;
  color: rgba(254, 255, 179, 0.5);
  font-weight: 400;
  font-size: 2.5rem;
}

.title {
  width: 100%;
  text-align: start;
  color: #feffb3;
  font-size: 1.5rem;
  font-weight: 300;
  letter-spacing: 0.25rem;
  margin-bottom: 3rem;
}

.toggle {
  position: absolute;
  top: 50%;
  right: 1.5rem;
  transform: translateY(-50%);
  width: 2rem;
  height: 2rem;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrapper {
  left: 0;
  top: 0;
  position: absolute;
  width: 100%;
  height: 100vh;
  background-image: url("/public/images/pattern.webp");
  background-repeat: repeat;
  background-size: 600px 600px;
  image-rendering: pixelated;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 5;
}

@media (max-width: 768px) {
  .wrapper_header {
    padding: 0 1.5rem;
    margin: 2rem 0 2.5rem 0;
  }
  .wrapper_header h2 {
    position: absolute;
    font-size: 1.25rem;
    line-height: 1;
    padding: 0;
    margin: 0;
    transform: translateX(50%);
    right: 50%;
  }

  .wrapper_header button {
    margin-left: auto;
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .wrapper button svg {
    width: 1rem;
    height: 1rem;
  }

  .container {
    max-width: none;
    transform: translateY(0);
    padding: 0 1rem 0 1rem;
    position: static;
  }

  .buttons {
    gap: 2rem;
    margin-bottom: 2.5rem;
  }

  .nav-button {
    color: rgba(254, 255, 179, 0.5);
    font-weight: 400;
    font-size: 1rem;
  }

  .title {
    font-size: .75rem;
    letter-spacing: 2px;
    margin-bottom: 1.5rem;
  }

  .input {
    border: 1px solid #fbe0f3;
    height: 100%;
    max-width: none;
    width: 100%;
    font-size: .75rem;
    color: #fbe0f3;
    padding: 1rem;
  }

  .input::placeholder {
    font-size: .75rem;
    letter-spacing: 2px;
  }

  .toggle img {
    width: 1rem;
    height: 1rem;
  }

  .toggle {
    right: .5rem;
  }

  .input_container {
    gap: .5rem;
    margin-bottom: 1.25rem;
  }

  .btn {
    padding: .75rem 5rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1rem;
    border: 1px solid #fbe0f3;
    letter-spacing: 2px;
    margin-top: 2.5rem;
    margin-bottom: 1.5rem;
  }

  .btn svg {
    width: 1rem;
    height: 1rem;
    margin-left: .5rem;
  }

  .agreement {
    font-size: .5rem;
    line-height: .5rem;
    color: #fbe0f3;;
    text-wrap: nowrap;
  }

  .forgot_password {
    font-size: .5rem;
    line-height: .5rem;
    letter-spacing: 1px;
  }

  .error {
    font-size: .5rem;
    letter-spacing: 2px;
  }

  .utils P:nth-of-type(1) {
    position: absolute;
    left: 50%;
    transform: translate(-50%, -1rem);
  }
}